<template>
	<div>
		<div class="container center banner">
			<img src="../../static/img/banner2.png" class="img-responsive pc-bg" />
			<img src="../../static/img/m-banner2.png" class="img-responsive m-bg" />
		</div>
		
		<div class="container center info-content">
			<div class="col-lg-3 col-md-3 col-sm-12 col-xs-12">
				<!--pc-->
				<div class="pc-item-nav">
					<div class="zhaosheng">
						<a href="#">
							<router-link to="/sign_online">
								<img src="../../static/img/slide1.png" class="img-responsive" />
							</router-link>
						</a>
						<a href="#">
							<router-link to="/special_major_accouting">
								<img src="../../static/img/slide2.png" class="img-responsive" />
							</router-link>
						</a>
					</div>
				</div>
			</div>
			<div class="col-lg-9 col-md-9 col-sm-12 col-xs-12 info">
				<div class="info-title">
					<h3>招生快讯</h3>
					<div class="info-title-right pc-bg">
						<i class="iconfont icon-zhengfangxing"></i>
						<span><router-link to="/">首页</router-link></span>
						<span>></span>
						<span><router-link to="/register_guide">招生快讯</router-link></span>
						<span>></span>
						<span>正文</span>
					</div>
				</div>
				<div class="col-lg-12 col-md-12 col-sm-12 col-xs-12 content">
					<div class="news">
						<span class="h3">{{news.title}}</span>
						<div class="news-info">
							<span>时间：{{news.add_time}}</span> 
						</div>
						<div v-html="news.content" class="page"></div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data(){
		return{
			news:'',
		}
	},
	created:function(){
    	var _self = this;
    	var newsId = this.$route.params.id;
    	document.body.scrollTop = 0;
    },
	activated:function(){
		
	  	 	 		
	},
	beforeRouteEnter (to, from, next) {
		next(vm => {
		    var _self = vm;
			var newsId = vm.$route.params.id;
//			console.log(typeof(newsId))
	　　　	var url="http://api.7171.la/api/artdetail";            
		  	vm.$http.post(url,{token:'gzsgzdzy',code:'18b304b9c103d44eca20b919dc99329e',id:newsId}
				).then(function (data) {
	    		var json = data.data;             
		  		_self.news = json;
		  		_self.$nextTick(() => {
					document.title=_self.news.title+"_韶关市中等职业技术学院 （广州校区）重点特色专业网";
				});
		  	});
	  	});
	},
    watch: {
        '$route': function () {
            this.newsId = this.$route.params.id;
        }
    },
	activated(){
		 document.documentElement.scrollTop = 0
	}
}
</script>

<style>
</style>


