		        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
		        <h3 class="modal-title text-center">录取查询</h3>
	        </div>
		    <div class="modal-content">
		      <form>
				  <div class="form-group">
				    <label for="exampleInputEmail1">考生名字：</label>
				    <input type="text" class="form-control" id="exampleInputEmail1" placeholder="请输入你的名字">
				  </div>
				  <div class="form-group">
