</template>

<script>
import {dotdotdot} from '../../static/js/jquery.dotdotdot.js'
export default {
	data(){
        return{
        	totalList: [],
        	//kuaixun
        	guideList:[],
        	recommand:'',
        	//policy
        	policyList:[],
        	identity:''
        }
    },
    mounted() {
    	
    },
	created:function(){
    	var _self = this;
　　　	var url="http://api.7171.la/api/article";            
		  this.$http.post(url,{token:'gzsgzdzy',code:'18b304b9c103d44eca20b919dc99329e',type:'22,23'}
			).then(function (data) {
       		var json = data.data;
		  	_self.totalList = json;
//		  	console.log(json)
		  	var j =0;
			for (var i = 0;i<_self.totalList.length;i++) {
				if(_self.totalList[i].type == "22"){
					if(_self.guideList.length<5){
						if(j==0){
							_self.recommand = _self.totalList[i];
							j++;
						}else{
							_self.guideList.push(_self.totalList[i]);
						}
					}
				}
				if(_self.totalList[i].type == "23"){
					if(_self.policyList.length<4){
						_self.policyList.push(_self.totalList[i]);
					}
				}
			}   
			_self.$nextTick(() => {
				$('.recommand span').dotdotdot();
			});
    	});
    },
    methods: {
	    select(uri){
	    	this.$router.push({ path: uri });
	    },
	    activated(){
	    	document.title = "韶关市中等职业技术学院 （广州校区）重点特色专业网";
	    	document.documentElement.scrollTop = 0
	    },
	    validate(str){
	       	var reg= /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/; 
			if(reg.test(str) === false) { 
				document.getElementById('identity').style.display = 'block';
				document.getElementById('btn').setAttribute('disabled','disabled');
	       	}else{
	       		document.getElementById('identity').style.display = 'none';
	       		$("#btn").removeAttr("disabled");
	       	}
    	},
    	submitForm(event) {
            event.preventDefault();
            validate(this.identity);
            
            let config = {
              headers: {
                'Content-Type': 'multipart/form-data'
              }
            }
 
      /*      this.$http.post('/upload', formData, config).then(function (res) {
              if (res.status === 2000) {
	//	                这里做处理
              }
            })*/
        }
	}
}
</script>

<style>
	.modal-dialog{
		background: #fefefe;
		border-radius: 10px;
		-moz-border-radius: 10px;
		-webkit-box-shadow: 0 3px 9px rgba(0,0,0,.5);
    	box-shadow: 0 3px 9px rgba(0,0,0,.5);
    	margin: 150px auto;
	}
	.modal-header{
		border: 0;
	    padding: 15px 15px 0;
	}
	.modal-content{
    	box-shadow: initial; 
    	border-radius: 0;
    	-webkit-border-radius: 0;
    	border: 0;
	}
	.modal-header h3{
		color: #08734b ; 
		font-weight: bolder;
		font-size: 20px;
	}
	.modal-footer {
	    padding: 10px;
	    text-align: center;
	    border-top: 0; 
	}
	.form-group {
	    width: 90%;
	    margin: 5px auto;
	}
	.btn{
		width: 70%;
		background: #1e9d6e ;
		border-color: #1e9d6e ;
	}
	form label{
		color: #084830 ;
	}
	.form-group i{
		display: none;
		float: right;
	    font-size: 13px;
	    font-style: normal;
	    color: #eb1717;
	    line-height: 20px;
	    margin-bottom: 5px;
	}
	body.modal-open {
		overflow-y: auto !important;
		padding-right: 0 !important;
	}
</style>


