import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
//kuanxun
import News from '../components/newsPage1'
//zhengce
import News1 from '../components/newsPage2'
//jianzhang
import News2 from '../components/newsPage3'
//jihua
import News3 from '../components/newsPage4'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
 		{
    	path: '/sc_info',
    	component: resolve => require(['@/components/sc_info'],resolve)
    },
    {
    	path: '/register_guide',
    	component: resolve => require(['@/components/register_guide'],resolve)
    },
    {
    	path: '/admission_policy',
    	component: resolve => require(['@/components/admission_policy'],resolve)
    },
    {
    	path: '/school_admission_brochure',
    	component: resolve => require(['@/components/school_admission_brochure'],resolve)
    },
    {
    	path: '/enrollment_plan',
    	component: resolve => require(['@/components/enrollment_plan'],resolve)
    },
    {
    	path: '/special_major_accouting',
    	component: resolve => require(['@/components/special_major_accouting'],resolve)
    },
    {
    	path: '/special_major_insidedesign',
    	component: resolve => require(['@/components/special_major_insidedesign'],resolve)
    },
    {
    	path: '/special_major_net',
    	component: resolve => require(['@/components/special_major_net'],resolve)
    },
    {
    	path: '/sign_online',
    	component: resolve => require(['@/components/sign_online'],resolve)
    },
    {
    	path: '/contact',
    	component: resolve => require(['@/components/contact'],resolve)
    },
    {	
    	//kuaixun
    	path: '/newsPage1/:id',
    	component: News,
    	name:'news'
    },
    {	//zhengce
    	path: '/newsPage2/:id',
    	component: News1,
    	name:'news1'
    },
    {	//jianzhang
    	path: '/newsPage3/:id',
    	component: News2,
    	name:'news2'
    },
    {	//jihua
    	path: '/newsPage4/:id',
    	component: News3,
    	name:'news3'
    }
  ]
})



